.aboutPage {
    margin-top: 10%;
}
.container {
    overflow-x: hidden !important;
}
.container h3 {
    text-align: center;
    padding-bottom: 20px;
}
.aboutPage h2 {
    color: #4b7a90;
    overflow: hidden;
    white-space: nowrap;
    border-right: 2px solid #4b7a90;
    z-index: 4;
    font-family: 'Forum';
    font-size: 6.3vw;
    
    animation: typewriter 2s steps(44) 1s 1 normal both,
        blinkTextCursor 400ms steps(44) infinite;
}
.aboutPage p {
    font-size: 17px;
    font-family: 'Source Sans Pro';
    font-weight: 200;
    margin-left: 20%;
    text-align: justify;
}
#image01 img{
    width: 350px;
    height: auto; 
    border-radius: 50%;
    margin-top: -10vh;
    margin-left: 6vw;
    padding-bottom: 100px;
}
.row-1 {
    margin-top: 5vh;
    min-height: 50vh;
}
.row-2 {
    padding-top: 0px;
    margin-top: 80px;
    padding-bottom: 100px;
}
#image02 img{
    width: 75%;
    height: auto;
    margin-left: 100px !important;
    margin-top: -80px !important;
}
.row-2 h3{
    font-family: "Forum";
    margin-left: -90px !important;
}
.row-2 p {
    margin-left: 30px !important;
    width: 480px;
}
.row-3 {
    border-left: 1px solid black; 
    margin-bottom: 60px;
}
#table-info {
    text-align: center;
}
#table-info h3 {
    font-family: 'Forum';
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 30px;
}
#table-info.container{
    padding-left: 115px;
    padding-right: 115px;
    padding-bottom: 150px;
}

#table-info h4, #table-info h5, #table-info p {
    font-family: 'Source Sans Pro';  
}
#table-info h4 {
    font-size: 20px;
    margin-bottom: 30px;
}
#table-info h5  {
    font-size: 17px;
    margin-top: 50px;
    margin-bottom: 150px;
    font-weight: bold;
}
#table-info p {
    margin-left: 0px;
    padding-left: 30px;
    padding-right: 30px;
}
#border-right {
    border-right: 1px solid black;
}

@keyframes typewriter {
    from {
        width: 0;
    }
    to {
        width: 36vw;
    }
}
@keyframes blinkTextCursor {
    from {
        border-right: 2px solid #4b7a90;
    }
    to {
        border-right: 2px solid transparent;
    }
}

@media screen and (min-height: 630px) and (max-width: 1200px) {
    .aboutPage {
        margin-top: 100px;
    }
    .row-1 {
        min-height: 0;
    }
    .row-1 p {
        margin-left: 0px;
    }
    #image01 img {
        margin-top: 0px;
        margin-left: 0px;
    }
    #image01 {
        text-align: center;
    }
    #image02 img{
        width: 0px;
    }
    .row-2 {
        margin-top: 0px;
        padding-bottom: 0px;
    }
    .row-2 h3, .row-2 p {
       margin-left: 0px !important;
       width: auto;
    }
    .col-6 {
        min-width: 100%;
    }
    #table-info.container {
        padding: 0px;
        overflow: hidden;
    }
    #table-info h3 {
        margin-bottom: 0px;
    } 
    .row-3 {
        border-left: none;
        margin-bottom: 0px;
        padding-bottom: 20px;
        padding-top: 20px;
        border-bottom: 1px solid black;
    }
    .col-sm-4 {
        flex: none !important;
        max-width: none !important;
        border-right: none;
    }
    #table-info h5 {
        margin-bottom: 200px !important;
    }
}
