        padding-bottom: 20px;
        padding-top: 20px;
        border-bottom: 1px solid black;
        width: 80vw !important;
        margin: 0 auto;
        border-right: none !important;
    }
    .col-sm-4 {
        flex: none !important;
        max-width: none !important;
        border-right: none;
    }
}
@media screen and (max-width: 1000px) { 
    .aboutPage {
        margin-top: 100px;
    }
    .aboutPage h2 {
        text-align: center;
        font-size: 40px;
    }
    #image01 {
        text-align: center;
    }
    #image01 img {
        width: 250px;
        height: auto;
        margin: 0 auto;
    }
    .row-2 h3, .row-2 p {
       margin-left: 0px !important;
       width: auto;
    }
    .col-6 {
        width: 80vw !important;
    }
    #table-info.container {
        padding: 0px;
        overflow: hidden;
    }
    #table-info h3 {
        margin-bottom: 0px;
        margin-top: 50px;
    } 
    #table-info h4 {
        margin-top: 20px;
    }
    #table-info p {
        margin-top: -15px;
    }
    .row-3 {
        border-left: none;
        margin-bottom: 0px;
        padding-bottom: 20px;
        padding-top: 0px;
        border-bottom: 1px solid black;
    }
    .col-sm-4 {
        flex: none !important;
        max-width: none !important;
        border-right: none;
    }
    
@keyframes typewriter {
    from {
        width: 0;
    }
    to {
        width: 250px;
    }
}
@keyframes blinkTextCursor {
    from {
        border-right: 2px solid #4b7a90;
    }
    to {
        border-right: 2px solid transparent;
    }
}
}