.homePage{
    left: 0;
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}
.homePage div {
    margin-top: 30vh;
    position: absolute;
    left: 47%;
}
.homePage h1 {
    margin-top:0;
    padding-top: 0;
    font-family: 'Forum', cursive;
    font-size: 10vw;
    margin-bottom: 0px;
    letter-spacing: 1px;
    color: #cd919e;
    border-bottom: 1px solid #3a3a3a; 
}
.homePage h2 {
    margin-top: 30px;
    font-size: 15px;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 200;
    letter-spacing: 4px;
    color: #3a3a3a;
}
.Image01 {
    width: 29% !important;
    height: auto !important;
    bottom: -2px;
    position: absolute;
    left: 15%;
}
@media screen and (max-width: 1000px){
    .homePage {
        text-align: center;
    }
    .homePage div {
        position: static;
        width: 70vw;
        margin: 0 auto;
        margin-top: 40vh;
    }
    .Image01 {
        visibility: hidden;
    }
}

.hyperlinks {
    transition-timing-function: 'ease-out',
}
.hyperlinks a{
    color: #3a3a3a;
    position: fixed;
    z-index: 1000;
    font-size: 45px;
    font-family: 'Londrina Outline';
    letter-spacing: 3;
    -webkit-text-decoration: 'none';
            text-decoration: 'none';
    margin: 20px;
    transition: '1s';
}
a.hyperlinks-nav:hover {
    font-family: 'Londrina Solid', cursive !important;
    color: #8c8973 !important;
}
.hyperlinks #up-left{
    left: 0;
    float:left;
    top: 0;
}
.hyperlinks #up-right{
    right: 0;
    float:right;
    top: 0;
}
.hyperlinks #down-left{
    left: 0;
    float:left;
    bottom: 0;
}
.hyperlinks #down-right{
    right: 0;
    bottom: 0;
    float: right;
}
.text-2 img, .text-3 img, .text-4 img{
    width: 15px;
    z-index: 1000;
}
.text-1 {
