.project-page {
    text-align: center;
    margin-top: 100px;
}
.project-page h1{
    position: fixed;
    margin-left: 10vw !important;
    font-family: 'Forum';
    font-size: 70px;
    color: #906fa8;
}
.project-page-landing {
    width: 100%;
    display:flex;
    transition: ease-in;
}
.project-page-landing div, .project-page-landing span  {
    margin-top: 100px;
    margin-right: 100px;
}
.front-picture {;
    min-width: 50%;
